.content h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 50px;
}

.overlay {
  background: linear-gradient(rgba(2, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.container {
  background-color: #575757;
  transform-origin: top left;
  transition: transform 0.4s linear;
  width: 100vw;
  height: auto;
  position: relative;
}

.container.show-nav {
  transform: translate(175px);
}

.container.show-nav + nav li {
  transform: translateX(0);
}

.container.show-nav .circle {
  transform: rotate(-70deg);
}

.container.show-nav::before {
  content: "";
  background: linear-gradient(rgba(2, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.circle-container {
  position: fixed;
  top: -100px;
  left: -100px;
  z-index: 10;
}

.circle {
  backdrop-filter: blur(10px) brightness(0.4);
  height: 200px;
  width: 200px;
  border-radius: 50%;
  position: relative;
  transition: transform 0.5s linear;
}

.circle.show-nav {
  transform: rotate(-90deg);
}

.circle button {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100px;
  background: transparent;
  border: 0;
  font-size: 26px;
  color: #fff;
}

.circle button:focus {
  outline: none;
}

.circle button#open {
  left: 60%;
}

.circle button#close {
  top: 60%;
  transform: rotate(90deg);
  transform-origin: top left;
}

nav {
  position: fixed;
  top: 100px;
  left: 0;
  z-index: 100;
  position: absolute;
  left: -16rem;
  top: 10rem;
}

nav.active {
  left: 0rem;
}

nav ul {
  list-style: none;
  padding-left: 15px;
}

nav ul li {
  text-transform: uppercase;
  color: #fff;
  margin: 40px 0;
  transform: translateX(-150%);
  transition: transform 0.4s ease-in;
}
nav ul li a {
  padding: 2rem 2rem 2rem 0;
}

nav ul li i {
  font-size: 20px;
  margin-right: 10px;
}

.content-home {
  background: #191926;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
}
.content-home .before {
  background-image: url("/build/img/cmsRC6.2.1-min.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  animation: 80s ease 0s normal none infinite running zoomEffect;
  z-index: 0;
}
@keyframes zoomEffect {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(2, 2);
  }
}
.content-home .after {
  position: absolute;
  top: 0;
  left: 0;
  width: 50vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: brightness(0.5) blur(10px);
  z-index: 0;
}
@media (max-width: 768px) {
  .content-home .after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    backdrop-filter: brightness(0.5) blur(10px);
  }
}
.content-home .bannertext {
  z-index: 2;
  width: 100%;
  padding: 2rem;
  text-align: center;
  color: #FFEBE7;
  display: flex;
  flex-direction: column;
