.container-contact .row100 {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.container-contact .row100 .col {
  position: relative;
  width: 100%;
  padding: 0 10px;
  margin: 30px 0 20px;
}

.container-contact .row100 .col .inputBox {
  position: relative;
  width: 100%;
  height: 40px;
  color: #fff;
}

.container-contact .row100 .col .inputBox input {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  padding: 0 10px;
  z-index: 1;
  color: #000;
}

.container-contact .row100 .col .inputBox .text-contact {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 40px;
  font-size: 18px;
  padding: 0 10px;
  display: block;
  transition: 0.5s;
  pointer-events: none;
}

.container-contact .row100 .col .inputBox input:focus + .text-contact,
.container-contact .row100 .col .inputBox input:valid + .text-contact {
  top: -35px;
  left: -10px;
}

.container-contact .row100 .col .inputBox .line-contact {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: 0.5s;
  border-radius: 2px;
  pointer-events: none;
}

.container-contact .row100 .col .inputBox input:focus ~ .line-contact,
.container-contact .row100 .col .inputBox input:valid ~ .line-contact {
  height: 100%;
}

.container-contact .row100 .col .inputBox.textarea {
  position: relative;
  width: 100%;
  height: 100px;
  padding: 10px 0;
}

.container-contact .row100 .col input[type=submit] {
